*{
margin:0;
padding:0;
box-sizing: border-box;

}

body {
  font-family:'montseratt', sans-serif;

}

.app{
  background-image: url('./assets/cold-bg.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease;
}

.app.warm{
  background-image: url('./assets/warm-bg.jpg');
}

main{
  min-height:100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgb(0,0,0,0.75));
  padding:25px;
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  display: black;
  width: 100%;
  padding: 15px;

  appearance: none;
  background:none;
  border: none;
  outline:none;

  background-color: rgba(255,255,255,0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25%;
  
  box-shadow: 0px 5px rgba(0,0,0,0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.location-box .location{
  color: #FFF;
  font-size: 32px;
  font-weight:500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position:relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #FFF;

  font-size: 102px;
  font-weight:900;

  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather-box .weather{
  color: #FFF;
  font-size: 48px;
  font-weight:700;
  box-shadow: 3px 3px rgba(50,50,70,0.5);

}

